/* App.css */

body {
  margin: 0;
  font-family: 'Arial', sans-serif;
  color: #333;
  line-height: 1.6;
}

.App {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
}

/* Header Section */
.header {
  background: #f4f4f4;
  padding: 2rem 1rem;
  text-align: center;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.photo {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 1rem;
}

.social-links a {
  margin: 0 10px;
  color: #0077b6;
  text-decoration: none;
  font-weight: bold;
}

.social-links a:hover {
  text-decoration: underline;
}

/* About Section */
.about {
  padding: 3rem 1rem;
  background: #ffffff;
}

.about h2 {
  margin-bottom: 1rem;
  color: #0077b6;
}

.about p {
  max-width: 800px;
  margin: 0 auto;
}

/* Experience Section */
.experience {
  padding: 3rem 1rem;
  background: #f9f9f9;
}

.timeline {
  max-width: 800px;
  margin: 0 auto;
}

.timeline-item {
  margin-bottom: 2rem;
}

.timeline-item h3 {
  color: #0077b6;
  margin: 0.5rem 0;
}

.timeline-item span {
  font-size: 0.9rem;
  color: #555;
}

/* Projects Section */
.projects {
  padding: 3rem 1rem;
  background: #ffffff;
}

.project-cards {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 1.5rem;
  max-width: 1000px;
  margin: 0 auto;
}

.card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 1rem;
  text-align: left;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  background: #ffffff;
}

.card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  border-radius: 4px;
}

.card h3 {
  margin-top: 1rem;
  color: #0077b6;
}

.card a {
  text-decoration: none;
  color: #0077b6;
  font-weight: bold;
}

.card a:hover {
  text-decoration: underline;
}

/* Skills Section */
.skills {
  padding: 3rem 1rem;
  background: #f9f9f9;
}

.skills-list {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 1rem;
}

.skill {
  background: #0077b6;
  color: #ffffff;
  padding: 0.5rem 1rem;
  border-radius: 4px;
  font-size: 0.9rem;
}

/* Contact Section */
.contact {
  padding: 3rem 1rem;
  background: #ffffff;
}

.contact form {
  max-width: 600px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.contact input,
.contact textarea {
  padding: 0.75rem;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 1rem;
}

.contact button {
  background: #0077b6;
  color: #ffffff;
  padding: 0.75rem;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
  font-weight: bold;
}

.contact button:hover {
  background: #005f8c;
}

/* Footer */
.footer {
  background: #f4f4f4;
  padding: 1rem;
  text-align: center;
  font-size: 0.9rem;
}

.footer-links a {
  margin: 0 10px;
  color: #0077b6;
  text-decoration: none;
}

.footer-links a:hover {
  text-decoration: underline;
}
